.frame {
    height: 100vh;
    overflow: hidden;
    .frame-header{
        display: flex;
        align-items: center;
        background-color: #5d70ea;
        color: #fff;
        &-logo{
            height: 50px;
            width: 100px;
            margin-right: 20px;
            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
        &-link{
            display: flex;
            flex-direction: row;
            margin-left: 15px;
            &-active{
                color: #444444 !important;
                background-color: #fff;
            }
            &-item{
                cursor: pointer;
                margin-right: 10px;
                height: 30px;
                line-height: 30px;
                border-radius: 6px;
                padding: 0 10px;
            }
        }
        &-right{
            display: flex;
            flex-direction: row;
            justify-items: flex-end;
            align-items: center;
            height: 100%;
            padding-left: 20px;
            &-search-wrap{
                position: relative;
                width: 100%;
            }
            &-text{
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
                align-items: center;
                height: 100%;
                width: 100%;
                padding-right: 10px;
                a{
                    padding: 0 10px;
                }
                
                .badge{
                    position: relative;
                    .dot{
                        width: 7px;
                        height: 7px;
                        border-radius: 50%;
                        position: absolute;
                        top: -5px;
                        right: 3px;
                        background: #ff8989;
                    }
                }
                .messageNum{
                    font-size: 25px;
                    color: #e3e8f07d;
                }
    
            }
            .frame-header-user {
                display: inline-flex;
                align-items: center;
            }
           
        }
        &-set, &-language,  &-user{
            margin: 0 10px 0 0;
        }
        &-dropdown{
            margin: 0 10px;
            >span {
                cursor: pointer;
                color:  #444444;
            }
        }
    }
    .frame-search-content{
        position: absolute;
        width: 100%;
        background: #fff;
        z-index: 1;
        overflow: auto;
        ul,li {
            list-style: none;
            margin: 0;
            padding: 0;
        }
    }
    .frame-header-name {
        width: 80px; 
        overflow: hidden;
        text-overflow: ellipsis; 
        white-space: nowrap;
        .ant-space-item {
            width: 60px;
        }
    }
    
}


.show {
    max-height: 350px;
    transition: max-height .3s ease-in;
    transform-origin: 50% 0;
    animation: slide-down 0.3s ease-in;
    -webkit-animation: slide-down 0.3s ease-in;
    padding: 20px;
}

.hidden {
    max-height: 0;
    overflow: auto;
    transition: max-height .3s ease-out;
}

@keyframes slide-down{
    0%{transform:scale(1,0);}
    100%{transform:scale(1,1);}
}

@-webkit-keyframes slide-down{
    0%{-webkit-transform:scale(1,0);}
    100%{-webkit-transform:scale(1,1);}
}

